import axios from "axios";
import {useQuery} from "react-query";

let profileUrl
let menuUrl

const urlFetch = fetch('https://dev.msvstudio.co.id/auth/conf.json')
   urlFetch.then( res => {
      if(res.status === 200)
         return res.json()   
   }).then( resJson => {
        profileUrl=resJson?.["profile-base"]
        menuUrl=resJson?.["menu-base"]   
   })


export const useSmartMenu = (username) => {
    const query = useQuery('keySmartMenu', () => axios.get(`${menuUrl+username}`).then(res => {
        return res.data;
    }, { refetchInterval: 1000 }))
    return {
        ...query,
        data: query.data
    }
}

export const useGetUsername =  (dataCookie) => {
    const query = useQuery('keyName', () => axios.get(`${profileUrl+dataCookie}`).then(res => {
        return res.data[0]
    },{ refetchInterval:1000}))
    return {
        ...query,
        data: query.data
    }
}